.trackerWrapper {
	position: absolute;
	top: 0px;
	left: 50%;
	transform: translate(-50%, 0);
	height: 120px;
	// width: 60vw;
	// padding-right: 100px; // Prevent tokens overlapping Next/Clear buttons
	border-bottom-left-radius: 5px;
	border-bottom-right-radius: 5px;

	transition: visibility 0s, top 400ms cubic-bezier(0.075, 0.82, 0.165, 1);
}

/*
	This isn't idea, really want this nested in .trackerWrapper but
	that doesn't play nice with the react modules
*/
.dm {
	padding-right: 100px; // Prevent tokens overlapping Next/Clear buttons
}

.hidden {
	top: -140px; // Slightly more than trackerWrapper height to allow for AC than hangs off bottom
	visibility: hidden;

	transition: visibility 0s 400ms, top 400ms cubic-bezier(0.28, 0.84, 0.42, 1);
}

$lineHeight: 24px;

.backLine {
	position: absolute;
	top: 50%;
	left: 0;
	right: 0;
	height: $lineHeight;
	transform: translateY(-50%);
	// background-color: #4277c6;
	background: linear-gradient(#4277c6, #4277c6, #28508d);
	z-index: 5;
	border-radius: $lineHeight / 2;
}

.tokenWrapper {
	position: relative;
	z-index: 10;
	padding: 15px 10px 20px 10px;
	display: flex;
	flex-direction: row;
	// max-width: 70vw; // Enabling this should help support smaller width windows
	// overflow-x: scroll; // Enabling this should help support smaller width windows
	// overflow-y: hidden; // Enabling this should help support smaller width windows
	justify-content: flex-start;
	align-items: center;
}

.buttonWrapper {
	position: absolute !important;
	right: 0;
	top: 50%;
	width: 90px;
	transform: translateY(-50%);
	z-index: 99;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

